.Card-box {
  display: grid;
  grid-auto-columns: 1fr;
  grid-template-areas:
    'header'
    'one'
    'two'
    'three'
    'four'
    'five'
    'six'
    'seven'
  ;
  width: 95vw;
  row-gap: 15px;
  column-gap: 15px;
  margin:auto;
  align-content: end;
  margin-top: 1vh;
  margin-bottom: 0;
  
  min-width: auto;
  min-height: auto;
  /* overflow: hidden; */
}



.image-container {
  grid-area: one;
}

.card:nth-child(3) {
  grid-area: two;
  /* border: 10px blue solid; */
}

.card:nth-child(4) {
  grid-area: three;
  /* border: 10px red solid; */
}

.card:nth-child(5) {
  grid-area: four;
  /* border: 10px red solid; */
}

.card:nth-child(6) {
  grid-area: five;
  /* border: 10px red solid; */
}

.card:nth-child(7) {
  grid-area: six;

  height: auto;
  background-image: url("/public/images/3.png") !important;
  background-size: cover;
  width: 100%;
  justify-self: center;
  border-radius: 25px;
  /* border: 10px red solid; */
  background-position: center 0%;
}

.card:nth-child(8) {
  grid-area: seven;

  height: auto;
  background-image: url("/public/images/2.png") !important;
  background-size: cover;
  width: 100%;
  justify-self: center;
  border-radius: 25px;
  /* border: 10px green solid; */
  background-position: center 40%;
}

.header {
  grid-area: header;
  /* border: 10px red solid; */
  height: 80vh;
  background-image: url("/public/images/1.png") !important;
}




@media only screen and (min-width: 600px) {

  /* For tablets: */
  .Card-box {
    grid-template-areas:
      "header header"
      "header header"
      "header header"
      "header header"
      "one two"
      "three three"
      "four five"
      "six seven";
    
  }

  
}

@media only screen and (min-width: 992px) {

  /* For desktop: */
  .Card-box {
    grid-template-areas:
      "header header header"
      "header header header"
      "header header header"
      "header header header"
      "one one three"
      "two two three"
      "four five six"
      "four five six"
      "seven seven seven";
  }
}

@media only screen and (min-width: 1200px) {

  /* For desktop: */
  .Card-box {
    grid-template-areas:
      "header header header one"
      "header header header two"
      "header header header three"
      "header header header three"
      "four five six seven"
      "four five six seven"
      "four five six seven";
  }
}
@media only screen and (max-width: 600px) {

  /* For mobile: */
  
  .Card-box { 
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    
  }
  .circles-container {
    flex-wrap: wrap!important;
    height: auto!important;
    width: auto;
  }
  .card {
    max-width: 95vw;
  }
  .header{
    max-width: 95vw;
  }
}

.image-container {
  filter: drop-shadow(-10px 10px 10px rgba(7, 9, 24, 0.45)) drop-shadow(-14px 16px 12px rgba(7, 9, 24, 0.45));
  -webkit-filter: drop-shadow(-10px 10px 10px rgba(7, 9, 24, 0.45)) drop-shadow(-14px 16px 12px rgba(7, 9, 24, 0.45));
  -moz-filter: drop-shadow(-10px 10px 10px rgba(7, 9, 24, 0.45)) drop-shadow(-14px 16px 12px rgba(7, 9, 24, 0.45));
  -ms-filter: drop-shadow(-10px 10px 10px rgba(7, 9, 24, 0.45)) drop-shadow(-14px 16px 12px rgba(7, 9, 24, 0.45));
  -o-filter: drop-shadow(-10px 10px 10px rgba(7, 9, 24, 0.45)) drop-shadow(-14px 16px 12px rgba(7, 9, 24, 0.45));
  min-height: 13vh;
  max-width: 100vw;
  margin: auto;
  width: 100%;
}